import * as React from 'react';
import Container from '@mui/material/Container';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { Divider } from '@mui/material';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import ToDo from './todo';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import { useState, useContext, useEffect } from 'react';
import { ToDoContext } from '../contexts/todoContexts';
import {v4 as uuidv4} from 'uuid';






export default function ToDoList() {


const [inputTitle,setInputTitle] = useState("");

const {todolist,setToDoList} = useContext(ToDoContext);

const [displayToDoType , setDisplayToDoType] = useState("all");





const completedToDo = todolist.filter((t) => t.isCompleted === true);
const nonCompletedToDo = todolist.filter((t) => t.isCompleted === false);




let filteredToDo = todolist;

if(displayToDoType === "completed"){
filteredToDo = completedToDo;
}else if(displayToDoType === "non-completed"){
filteredToDo = nonCompletedToDo;
}else{
filteredToDo = todolist;
}



const toDoArrayList = filteredToDo.map((t) => {
return(
<ToDo key={t.id} todo={t} />
)
})


useEffect(() => {
const storedToDoList = JSON.parse(localStorage.getItem("todolist"));
if (storedToDoList) {
setToDoList(storedToDoList);
}
}, [setToDoList]);



function handleChangeType(e){
setDisplayToDoType(e.target.value);

}






function handleAddTask(){
const newTask = {
id:uuidv4(),
title:inputTitle,
details:"",
isCompleted:false
}
const newToDoList = [...todolist,newTask];
setToDoList(newToDoList);

localStorage.setItem("todolist",JSON.stringify(newToDoList));
setInputTitle("");
}









return (
<Container maxWidth="sm" >
<Card sx={{ minWidth: 275 }} style={{maxHeight:"90vh",overflow:"scroll"}}>
    <CardContent>
    <Typography variant='h2' gutterBottom  style={{fontWeight:"bold"}}>
    مهامي
    <Divider style={{position:"relative",bottom:"15px"}}/>
    </Typography>
    </CardContent>
    {/* BUTTONS */}
    <ToggleButtonGroup
    style={{direction:"ltr"}}
    color="secondary"
    value={displayToDoType}
    exclusive
    onChange={handleChangeType}
    aria-label="Platform"
>
    <ToggleButton value="non-completed">غير منجز</ToggleButton>
    <ToggleButton value="completed">منجز</ToggleButton>
    <ToggleButton value="all"> الكل</ToggleButton>
</ToggleButtonGroup>
    {/* BUTTONS */}

    {toDoArrayList}    





<Grid container display="flex" justifyContent="center" spacing={2} style={{margin:"20px"}}>
<Grid xs={8}>
<TextField id="outlined-basic" label="عنوان المهمة" variant="outlined" 
value={inputTitle}
onChange={(e) => {
setInputTitle(e.target.value);
}}
/>

</Grid >
<Grid xs={4}>
<Button variant="contained" style={{fontWeight:"bold",fontSize:"24px"}}
onClick={handleAddTask}
disabled={inputTitle === ""? true : false}
>اضافة</Button>
</Grid>
</Grid>


</Card>

</Container>


);

}


