import { CardContent } from "@mui/material"
import React, { useContext, useState } from "react"
import Card from '@mui/material/Card';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import CheckIcon from '@mui/icons-material/Check';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { ToDoContext } from "../contexts/todoContexts";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from "@mui/material/TextField";

export default function ToDo({todo}){
const {todolist, setToDoList} = useContext(ToDoContext);
function handleCheckClick(){
        const newToDoList = todolist.map((t) => {
        if(t.id === todo.id){
        return {
        ...t,
        isCompleted:!t.isCompleted
        }
        }
        return t;
        
        
        })
        setToDoList(newToDoList);
        localStorage.setItem("todolist",JSON.stringify(newToDoList));
        
        
        
        
}

const [openDleteAlert, setOpenDeleteAlert] = useState(false);

const [openUpdateAlert, setOpenUpdateAlert] = useState(false);

const [updateInput , setUpdateInput] = useState({
    title:"",
    details:""
})


function handleDleteAlert (){
setOpenDeleteAlert(true);
}

function handleUpdateTaskInput (e){
e.preventDefault();
const newToDoList = todolist.map((t) => {
if(t.id === todo.id){
return {
...t,
title:updateInput.title,
details:updateInput.details
};
}
return t;
});
setToDoList(newToDoList);
setOpenUpdateAlert(false);
localStorage.setItem("todolist",JSON.stringify(newToDoList));
}


return(

<>
{/* DELETE MODAL */}

    <Dialog
    open = {openDleteAlert}
    aria-labelledby="alert-dialog-title"
    aria-describedby="alert-dialog-description"
    >
    
    <DialogContent>
        <DialogContentText id="alert-dialog-description" style={{fontWeight:"bold"}}>
        هل انت متأكد من حذف هذه المهمة؟
        </DialogContentText>
    </DialogContent>
    <DialogActions>
        <Button 
        onClick={() => setOpenDeleteAlert(false)}
        >لا</Button>
        <Button  autoFocus
        
        onClick={() => {
            const newToDoList = todolist.filter((t) => t.id !== todo.id);
            setToDoList(newToDoList);
            setOpenDeleteAlert(false);
        localStorage.setItem("todolist",JSON.stringify(newToDoList));

        }}
        >نعم
            
        </Button>
    </DialogActions>
    </Dialog>

{/* DELETE MODAL */}

{/* UPDATE MODAL */}

    
    <Dialog
    open={openUpdateAlert}
    
    >
    <DialogTitle>تعديل</DialogTitle>
    <DialogContent >
        <DialogContentText>
        اضف معلومات المهمة
        </DialogContentText>
        <TextField 
        autoFocus
        required
        margin="dense"
        label="عنوان المهمة"
        fullWidth
        variant="standard"
        value={updateInput.title}
        onChange={(e) => {
        setUpdateInput({
        ...updateInput,
        title:e.target.value
        })
        }}
        />
        <TextField
        
        margin="dense"
        label="تفاصيل المهمة"
        fullWidth
        variant="standard"
        value={updateInput.details}
        onChange={(e) => {
        setUpdateInput({
        ...updateInput,
        details:e.target.value
        })
        }}
        />
    </DialogContent>
    <DialogActions>
        <Button onClick={() => {
        setOpenUpdateAlert(false);
        }}>الغاء</Button>
        <Button type="submit"

        onClick={handleUpdateTaskInput}
        >تعديل</Button>
    </DialogActions>
    </Dialog>

{/* UPDATE MODAL */}



<Card className="task-card" sx={{ minWidth: 275, background:"#283593", margin:"10px 10px",color:"white"}}>
    <CardContent >
    {/* TASK TITLE */}
    <Grid container spacing={2} >
    <Grid size={8}>
    <Typography variant='h5' gutterBottom  style={{textAlign:"right"}}>
    {todo.title}
    </Typography>
    <Typography variant='h6' gutterBottom  style={{textAlign:"right"}}>
    {todo.details}
    </Typography>
    </Grid>
    {/* TASK TITLE */}
    
    {/* ICONS */}
    <Grid size={4} display="flex" justifyContent="space-around" alignItems="center">
    <CheckIcon className="icon" style={{color:todo.isCompleted ? "white" : "green",background:todo.isCompleted ? "green" : "white",borderRadius:"50%",cursor:"pointer",fontSize:"30px"}} 
    
    onClick={handleCheckClick}
    />    
    <DeleteIcon className="icon" style={{color:"red",background:"white",borderRadius:"50%",cursor:"pointer",fontSize:"30px"}}
    onClick = {handleDleteAlert}
    />
    <EditIcon className="icon" style={{color:"blue",background:"white",borderRadius:"50%",cursor:"pointer",fontSize:"30px"}}
    onClick= {() => {
    setOpenUpdateAlert(true);
    setUpdateInput({
    title:todo.title,
    details:todo.details
    })
    }}
    />
    {/* ICONS */}

    </Grid>
    
    </Grid>

    </CardContent>

</Card >    
    

</>

)
}